import { Component, h, Element, Prop, State, Listen } from '@stencil/core';

@Component({
  tag: 'ks-order-check-section',
})
export class OrderCheckSection {

    @Prop() label: string;
    @Prop() name: string;

    @Element() root: HTMLElement;

    @Prop({mutable: true, reflect: true}) active: boolean = false;
    ChangeHandler() {
        const input = this.root.querySelector(`div > label > input`) as HTMLInputElement;
        this.active = input.checked;

        const inputs = this.root.querySelectorAll("ks-input-text");
        inputs.forEach(input => {
            input.message = "";
        });
    }

    @State() mobile: boolean = false;
    @Listen('resize', { target: 'window' })
    resizeHandler() {
      this.mobile = window.innerWidth < 959 ? true : false;
    }

    componentDidLoad(){
        this.root.style.display = "block";
        this.resizeHandler();
        this.ChangeHandler();
    }

    render() {
        return (
            <div>
                <label class="uk-display-block uk-margin">
                    <input type="checkbox" class="uk-checkbox"
                           style={{transform: "scale(1.4) translateX(2px)", marginBottom: "3px"}} onChange={()=>this.ChangeHandler()}></input>

                    <span class="uk-margin-left ks-text-decorated uk-text-bold" style={{fontSize: "22px"}}>{this.label}</span>
                </label>
                <br />

                {this.active ? null : 
                    <input type="hidden" name={this.name} value="1" />
                }
                

                <div class="uk-inline uk-width-1-1" hidden={!this.active && this.mobile}>
                    <slot />

                    { this.active ? null :
                        <div class="uk-overlay uk-overlay-default uk-position-cover" style={{opacity: "0.8"}}></div>
                    }
                </div>
                
            </div>
        )
    }
}